<template>
    <div class="search">
        <div class="search-box-wrapper">
            <search-box ref="searchBox" @query="search"></search-box>
        </div>
        <div class="shortcut-wrapper"  v-show="!query" ref="shortcutWrapper">
            <scroll class="shortcut" ref="shortcut" :data="shortcut">
                <div>
                    <div class="hot-key">
                        <h1 class="title">热门搜索</h1>
                        <ul>
                            <li v-for="item in hotKey" class="item" @click="addQuery(item.k)">
                                <span> {{item.k}} </span>
                            </li>
                        </ul>
                    </div>
                    <div class="search-history" v-show="searchHistory.length">
                        <h1 class="title">
                            <span class="text">搜索历史</span>
                            <span @click="showConfirm" class="clear"><i class="icon-clear"></i></span>
                        </h1>
                        <search-list @delete="deleteSearchHistory" @select="addQuery" :searches="searchHistory"></search-list>
                    </div>
                </div>
            </scroll>
        </div>
        <div class="search-result" v-show="query" ref="searchResult">
            <suggest  ref="suggest" :query="query" @select="saveSearchHistory" @scrollStart="blurInput"></suggest>
        </div>
        <confirm ref="confirm" @confirm="clearSearchHistory" text="是否清空所有搜索历史" confirmBtnText="清空"></confirm>
        <router-view></router-view>
    </div>
</template>

<script type="text/ecmascript-6">
    import SearchBox from '@/base/search-box/search-box'
    import SearchList from '@/base/search-list/search-list'
    import scroll from '@/base/scroll/scroll'
    import confirm from '@/base/confirm/confirm'
    import suggest from '@/components/suggest/suggest'
    import {getHotKey} from '@/api/search'
    import {ERROR_OK} from '@/api/config'
    import {mapGetters, mapActions} from 'vuex'
    import {playlistMixin, searchMixin} from '@/common/js/mixin'

    export default {
        mixins: [playlistMixin, searchMixin],
        data() {
            return {
                hotKey: [],
                query: ''
            }
        },
        computed: {
            shortcut() {
                return this.hotKey.concat(this.searchHistory)
            }
        },
        created() {
            this._getHotKey()
            this.searchHistory = []
        },
        methods: {
            handlePlaylist(playlist) {
                const bottom = playlist.length > 0 ? '60px' : ''

                this.$refs.searchResult.style.bottom = bottom
                this.$refs.suggest.refresh()

                this.$refs.shortcutWrapper.style.bottom = bottom
                this.$refs.shortcut.refresh()
            },
            _getHotKey() {
                getHotKey().then((res)=>{
                    if (res.code === ERROR_OK) {
                        this.hotKey = res.data.hotkey.slice(0, 10)
                    }
                })
            },
            showConfirm() {
                this.$refs.confirm.show()
            },
            ...mapActions([
                'saveSearchHistory',
                'deleteSearchHistory',
                'clearSearchHistory'
            ])
        },
        components: {
            SearchBox,
            SearchList,
            suggest,
            confirm,
            scroll
        }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"
    .search
        .search-box-wrapper
            margin: 20px
        .shortcut-wrapper
            position: fixed
            top: 134px
            bottom: 0
            .shortcut
                height: 100%
                overflow: hidden
                width: 100%
                .hot-key
                    margin : 0 20px 20px 20px
                    .title
                        margin-bottom : 20px
                        font-size: $font-size-medium
                        color: $color-text-l
                    .item
                        display: inline-block
                        padding: 5px 10px
                        margin: 0 20px 10px 0
                        border-radius: 6px
                        background: $color-highlight-background
                        font-size: $font-size-medium
                        color: $color-text-d
                .search-history
                    position: relative
                    margin: 0 20px
                    .title
                        display: flex
                        align-items: center
                        height: 40px
                        font-size: $font-size-medium
                        color: $color-text-l
                        .text
                            flex: 1
                        .clear
                            extend-click()
                            .icon-clear
                                font-size: $font-size-medium
                                color: $color-text-d
        .search-result
            position: fixed
            width: 100%
            top: 134px
            bottom: 0
</style>



